import React, { useEffect } from "react";
import { connect } from "react-redux";
import RoomGallery from "../RoomGallery";
import Banner from "../Common/Banner";
import AmenitiesContainer from "./AmenitiesContainer";
import OptionalPackagesContainer from "./OptionalPackagesContainer";
import HotDealContainer from "./HotDealContainer";

const RoomDetailPage = ({ rooms, lang, ...others }) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const mockData = {
    executive_lodge: [
      "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/rooms%2Froom1%2Froom1_1.jpg?alt=media",
      "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/rooms%2Froom1%2Froom1_2.jpg?alt=media",
      "https://firebasestorage.googleapis.com/v0/b/react-830f4.appspot.com/o/rooms%2Froom1%2Froom1_3.jpg?alt=media"
    ],
    confort: []
  };

  const roomId = others.match.params && others.match.params.id;
  const roomIdDeal = "executive_lodge";
  const selectedRoom = rooms.find(room => {
    return room.id === roomId;
  });

  const selectedRoomDeal = rooms.find(room => {
    return room.id === roomIdDeal;
  });
  if (!selectedRoom) return null;

  return (
    <div className="opalhotel-single opal-hotel-room-booking opalhotel_room-template-default single single-opalhotel_room postid-137">
      <Banner
        name={selectedRoom && selectedRoom.name && selectedRoom.name[lang]}
      />
      <div className="container content-area">
        <div className="middle-align">
          <div className="opalhotel-wrapper">
            <div className="opalhotel-main-wrapper">
              <div className="site-main" id="sitemain">
                <div className="opalhotel-wrapper">
                  <div
                    id="opalhotel-room-137"
                    className="post-137 opalhotel_room type-opalhotel_room status-publish has-post-thumbnail hentry"
                  >
                    <div className="opalhotel-main">
                      <h1 className="room-title">
                        {selectedRoom &&
                          selectedRoom.name &&
                          selectedRoom.name[lang]}
                      </h1>
                      <div className="opalhotel-price opalhotel-room-price">
                        <span className="price-value">
                          $
                          <b
                            data-thousand=","
                            data-decimal="."
                            data-numberdecimal="1"
                            data-symbol="$"
                            data-format="0,0.0"
                            data-price={selectedRoom && selectedRoom.price}
                          >
                            {selectedRoom && selectedRoom.price}
                          </b>
                        </span>
                        <span className="price-title">From</span> /{" "}
                        <span className="price-unit"> per night</span>
                      </div>

                      <RoomGallery data={mockData[selectedRoom.id]} />
                      <AmenitiesContainer
                        description={
                          selectedRoom &&
                          selectedRoom.longDescription &&
                          selectedRoom.longDescription[lang]
                        }
                      />
                      <OptionalPackagesContainer />
                    </div>
                  </div>
                </div>
              </div>
              <div id="sidebar" className="opalhotel-sidebar">
                <aside
                  id="opalhotel-check-available-2"
                  className="widget widget_opalhotel-check-available"
                >
                  <div className="widget_content">
                    <h3 className="widget-title">
                      <span>Reservation</span>
                    </h3>
                    <button
                      type="submit"
                      className="opalhotel-button-submit button button-primary hover-link"
                    >
                      {rooms &&
                        rooms[0] &&
                        rooms[0].book &&
                        rooms[0].book[lang]}
                    </button>
                  </div>
                </aside>

                <aside
                  id="opalhotel-rooms-lastest-deals-2"
                  className="widget widget_opalhotel-rooms-lastest-deals"
                >
                  <HotDealContainer
                    detailLink="/rooms/executive_lodge"
                    name={
                      selectedRoomDeal &&
                      selectedRoomDeal.name &&
                      selectedRoomDeal.name[lang]
                    }
                  />
                </aside>
              </div>
              <div className="clear"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default connect(
  state => {
    return {
      room: state.room.roomDetail,
      rooms: state.room.list,
      lang: state.lang.currentLang
    };
  }
  // dispatch => ({
  //   setPackageDetail: data => dispatch(setRoomDetailAction(data))
  // })
)(RoomDetailPage);
